<template>
  <div>
    <search-box :searchData="searchData" :table="table"></search-box>
    <resize-table>
      <table class="table">
        <thead>
        <tr>
          <th width="40%">标题</th>
          <th width="15%">发布时间</th>
          <th width="30%">范围</th>
          <th width="15%">操作</th>
        </tr>
        </thead>
        <tbody>
        <tr v-for="item in table.list">
          <td>{{item.title}}</td>
          <td>{{item.createdTime | substrTime }}</td>
          <td><span v-for="scopeItem in item.scopeList">{{scopeItem.scopeName}}</span></td>
          <td>
            <span @click="$router.push(`/school/announcement/publicity?id=`+item.id+`&tabPick=`+tabPick+``)"
                  class="oper">编辑</span>
            <span @click="remove(item)" class="oper">删除</span>
          </td>
        </tr>
        </tbody>
      </table>
    </resize-table>
    <no-data v-show="!table.noData"></no-data>
    <paging :data.sync="table"></paging>
  </div>

</template>
<script>
  export default {
    data () {
      return {
        tabPick: 'parentalNotice',
        searchData: [{type: 'input', placeholder: '标题'}],
        table: {
          url: '/notice/queryNoticePage.do',
          el: '.table',
          total: 0,
          noData: true,
          list: [],
          query: {
            noticeType: 'PATRIARCH_NOTICE',
            keywords: null
          }
        }
      }
    },
    methods: {
      // 删除招聘公告
      async remove (item) {
        await this.$confirm(`您确认删除【${item.title}】吗?`, '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        })
        await this.$ajax({
          url: '/notice/deleteNotice.do',
          query: {
            id: item.id,
            isDelete: 'YES'
          }
        })
        this.$remind('删除成功')
        this.$search(this.table)
      }
    },
    mounted () {
      this.$search(this.table)
    }
  }
</script>
